@media only screen and (max-width: 600px) {
  .input-filter-responsive {
    display: block !important;
  }
}

.ant-upload-list {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  color: rgba(0, 0, 0, 0.65);
  font-size: 14px;
  font-variant: tabular-nums;
  line-height: 1.5;
  list-style: none;
  -webkit-font-feature-settings: "tnum";
  font-feature-settings: "tnum";
  zoom: 1;
  margin-left: 115px;
  margin-top: -39px;
}

a.ant-typography-expand:after {
  content: "Expandir";
  color: #1b512b;
  font-weight: 600;
  margin-left: -56px;
}

a.ant-typography-expand,
a.ant-typography-expand:hover {
  color: transparent;
}

.ant-list, .ant-list-vertical, .ant-list-lg, .ant-list-split, .ant-list-something-after-last-item{
  min-width: 200;
  min-height: 200px;
}

